<template>
  <div class="netflix">
    <main>
      <div class="main-overlay">
        <div class="logo">
          <img src="https://i.ibb.co/r5krrdz/logo.png" alt="Netflix Logo" />
        </div>
        <button class="sign-in-btn">Sign In</button>
        <div class="content-center">
          <h1>See what's next.</h1>
          <h2>Watch anywhere, cancel anytime.</h2>
          <button class="free-days-btn">Try 30 days free ></button>
        </div>
      </div>
    </main>
    <section class="offers">
      <div class="dynamic-btns">
        <button class="active" @click="currentDoor">
          <img src="@/assets/door.png" alt="Open Door" />
          <h3 class="hideSmall">
            No commitments
            <br />Cancel online at anytime
          </h3>
          <h3 class="showSmall">Cancel</h3>
        </button>
        <button @click="currentDevices">
          <img src="@/assets/devices.png" alt="Devices" />
          <h3 class="hideSmall">Watch anywhere</h3>
          <h3 class="showSmall">Devices</h3>
        </button>
        <button @click="currentTag">
          <img src="@/assets/tag.png" alt="Tag" />
          <h3 class="hideSmall">Pick your price</h3>
          <h3 class="showSmall">Price</h3>
        </button>
      </div>
      <component :is="currentComponent"></component>
    </section>
    <footer>
      <div class="container">
        <p>Questions? Contact us.</p>
        <ul>
          <li>FAQ</li>
          <li>Help Center</li>
          <li>Account</li>
          <li>Media Center</li>
          <li>Investor Relations</li>
          <li>Privacy</li>
          <li>Speed Test</li>
          <li>Jobs</li>
          <li>Cookie Preferences</li>
          <li>Legal Notices</li>
          <li>Ways to Watch</li>
          <li>Corporate Information</li>
          <li>Netflix Originals</li>
          <li>Terms of Use</li>
          <li>Contact Us</li>
        </ul>
      </div>
    </footer>
  </div>
</template>

<script>
function activeBtn() {
  const btns = document.querySelectorAll(".dynamic-btns button");
  btns.forEach(btn => btn.classList.remove("active"));
  event.target.classList.add("active");
}

import Door from "@/components/Door.vue";
import Devices from "@/components/Devices.vue";
import Tag from "@/components/Tag.vue";

export default {
  name: "Netflix",
  components: {
    Door,
    Devices,
    Tag
  },
  data() {
    return {
      currentComponent: "Door"
    };
  },
  methods: {
    currentDoor() {
      this.currentComponent = "Door";
      activeBtn();
    },
    currentDevices() {
      this.currentComponent = "Devices";
      activeBtn();
    },
    currentTag() {
      this.currentComponent = "Tag";
      activeBtn();
    }
  }
};
</script>

<style lang="scss" scoped>
main {
  width: 100vw;
  height: 93vh;
  background: url("https://i.ibb.co/vXqDmnh/background.jpg") no-repeat center
    center;
    background-size: cover;
  position: relative;
  .main-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
  }
  .logo {
    text-align: center;
    transform: scale(0.5);
  }
  .sign-in-btn {
    position: absolute;
    top: 1.5em;
    right: 4em;
    background-color: #dd0611;
    border: none;
    border-radius: 5px;
    color: #fff;
    padding: 0.45em 1.1em;
    font-size: 1em;
