    top: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    h1 {
      font-size: 5em;
      width: max-content;
    }
    h2 {
      font-size: 1.8em;
      margin: 1em 0;
      text-transform: uppercase;
    }
    .free-days-btn {
      background-color: #dd0611;
      border: none;
      border-radius: 5px;
      color: #fff;
      padding: 0.8em 2.5em;
      font-size: 2em;
      cursor: pointer;
      text-transform: uppercase;
      letter-spacing: 1px;
      white-space: pre;
      &:hover {
        background-color: lighten(#dd0611, 5%);
      }
    }
  }
}
.offers {
  .dynamic-btns {
    display: flex;
    justify-content: space-evenly;
    background-color: #141414;
    padding: 2em;
    border-bottom: 2px solid #555;
    button {
      background-color: transparent;
      padding: 0 5em;
      border: none;
      border-bottom: 3px #dd0611;
      cursor: pointer;
      text-align: center;
      opacity: 0.4;
      img {
        transform: scale(0.8);
        pointer-events: none;
      }
      h3 {
        color: #fff;
        pointer-events: none;
      }
      .showSmall {
        display: none;
      }
      &:hover {
        opacity: 1;
      }
      &.active {
        opacity: 1;
      }
    }
  }
}
footer {
