          text-decoration: underline;
        }
      }
    }
  }
}
// MEDIA QUERIES
@media (max-width: 1000px) {
  main {
    .main-overlay {
      .logo {
        position: absolute;
        left: -2em;
      }
    }
  }
  .offers {
    .dynamic-btns {
      button {
        img {
          transform: scale(0.6);
        }
        h3 {
          font-size: 1em;
        }
      }
    }
  }
  footer {
    .container {
      width: 90vw;
    }
  }
}
@media (max-width: 750px) {
  main {
    .main-overlay {
      background-color: rgba(0, 0, 0, 0.7);
